@import '../../_global.scss';

.rc-tree {
    margin: 0;
    border: 1px solid transparent;
  }
  .rc-tree-focused:not(.rc-tree-active-focused) {
    border-color: cyan;
  }
  .rc-tree .rc-tree-treenode {
    margin: 0;
    padding: 0;
    line-height: 24px;
    white-space: nowrap;
    list-style: none;
    outline: 0;
  }
  .rc-tree .rc-tree-treenode .draggable {
    color: #333;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    /* Required to make elements draggable in old WebKit */
    -khtml-user-drag: element;
    -webkit-user-drag: element;
  }
  .rc-tree .rc-tree-treenode.drop-container > .draggable::after {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    box-shadow: inset 0 0 0 2px red;
    content: "";
  }
  .rc-tree .rc-tree-treenode.drop-container ~ .rc-tree-treenode {
    border-left: 2px solid chocolate;
  }
  .rc-tree .rc-tree-treenode.drop-target {
    background-color: yellowgreen;
  }
  .rc-tree .rc-tree-treenode.drop-target ~ .rc-tree-treenode {
    border-left: none;
  }
  .rc-tree .rc-tree-treenode.filter-node > .rc-tree-node-content-wrapper {
    color: #a60000 !important;
    font-weight: bold !important;
  }
  .rc-tree .rc-tree-treenode ul {
    margin: 0;
    padding: 0 0 0 18px;
  }
  .rc-tree .rc-tree-treenode .rc-tree-node-content-wrapper {
    position: relative;
    display: inline-block;
    height: 24px;
    margin: 0;
    padding: 0;
    text-decoration: none;
    vertical-align: top;
    cursor: pointer;
  }
  
  .rc-tree .rc-tree-treenode span.rc-tree-switcher {
    display: inline-block;
    width: 14px;
    height: 14px;
    margin-right: 2px;
    line-height: 14px;
    vertical-align: -0.125em;
    background-color: transparent;
    background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='utf-8'%3F%3E%3Csvg width='14px' height='14px' viewBox='0 0 14 14' version='1.1' xmlns:xlink='http://www.w3.org/1999/xlink' xmlns='http://www.w3.org/2000/svg'%3E%3Cdesc%3ECreated with Lunacy%3C/desc%3E%3Cpath d='M0 7C0 10.864 3.136 14 7 14C10.864 14 14 10.864 14 7C14 3.136 10.864 0 7 0C3.136 0 0 3.136 0 7ZM7 1C3.6925 1 1 3.6925 1 7C1 10.3075 3.6925 13 7 13C10.3075 13 13 10.3075 13 7C13 3.6925 10.3075 1 7 1ZM3.5 7.58333L3.5 6.41667L10.5 6.41667L10.5 7.58333L3.5 7.58333Z' id='Button' fill='%230891CD' fill-rule='evenodd' stroke='none' /%3E%3C/svg%3E");
  }

  .rc-tree .rc-tree-treenode span.rc-tree-iconEle,
  .rc-tree .rc-tree-treenode span.rc-tree-checkbox {
      display: none;
  }

  .animation .rc-tree-treenode {
    display: flex;
  }
  .animation .rc-tree-treenode .rc-tree-indent {
    position: relative;
    align-self: stretch;
    display: flex;
    flex-wrap: nowrap;
    align-items: stretch;
  }
  .animation .rc-tree-treenode .rc-tree-indent .rc-tree-indent-unit {
    position: relative;
    height: 100%;
  }
  .animation .rc-tree-treenode .rc-tree-indent .rc-tree-indent-unit::before {
    position: absolute;
    top: 0;
    bottom: 0;
    border-right: 1px solid blue;
    left: 50%;
    content: '';
  }
  .animation .rc-tree-treenode .rc-tree-indent .rc-tree-indent-unit-end::before {
    display: none;
  }
  .animation .rc-tree-treenode .rc-tree-switcher-noop::before {
    content: '';
    display: inline-block;
    width: 16px;
    height: 16px;
    background: red;
    border-radius: 100%;
  }

  
  .rc-tree li a.rc-tree-node-selected .cus-label {
    background-color: white;
    border: none;
  }

  .rc-tree-contextmenu {
    position: absolute;
    left: -9999px;
    top: -9999px;
    z-index: 1070;
    display: block;
    background-color: #fff;
  }
  .rc-tree-contextmenu-hidden {
    display: none;
  }
  .rc-tree-contextmenu-inner {
    border: 1px solid #ddd;
    padding: 10px 20px;
  }

  

